export default {
	data() {
		return {};
	},
	computed: { ...mapState(['token', 'id', 'type', 'username', 'theIndex']) },
	methods: {
		...mapMutations(['theIndexStorage'])
	},
	onLoad() {
		this.theIndexStorage(2);
		// 修改底部stab样式
		uni.setTabBarItem({
			index: this.theIndex,
			selectedIconPath: '/static/tab/daili.png'
		});
	}
};
